.lf-control {
  display: flex;
  padding: 0 15px;
  background: rgba(255, 255, 255, 0.8);
  -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  margin: 5px;
  position: absolute;
  right: 10px;
  top: 0;
}
.lf-control-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  justify-content: center;
  padding: 5px 10px;
}
.lf-control-item:hover {
  background: #efefef;
}
.lf-control-item.disabled {
  filter: opacity(0.6);
  cursor: not-allowed;
}
.lf-control-item.disabled:hover {
  background: #fff;
}
.lf-control-item.disabled i {
  pointer-events: none;
}
.lf-control-item i {
  display: inline-block;
  width: 20px;
  height: 20px;
  vertical-align: middle;
  background-size: cover;
}
.lf-control-text {
  margin-top: 5px;
  font-size: 12px;
}
.lf-control-zoomOut {
  background-image: url(data:image/png;base64,);
}
.lf-control-zoomIn {
  background-image: url(data:image/png;base64,);
}
.lf-control-fit {
  background-image: url(data:image/png;base64,);
}
.lf-control-undo {
  background-image: url(data:image/png;base64,);
}
.lf-control-redo {
  background-image: url(data:image/png;base64,);
}

/*menu*/
.lf-menu {
  position: absolute;
  display: none;
  background: #fff;
  padding: 10px 0;
  margin: 0 0 0 10px;
  border-radius: 3px;
  border: 1px solid #efefee;
  width: 200px;
  z-index: 999;
}
.lf-menu > li {
  cursor: pointer;
  list-style: none;
  padding: 3px 12px;
  font-size: 12px;
  line-height: 18px;
  cursor: pointer;
  transition: all 120ms ease-in-out;
  position: relative;
}
.lf-menu-item__disabled {
  cursor: default;
  pointer-events: none;
  color: #aaa;
  opacity: 0.88;
}
.lf-menu-item:hover {
  background: #f3f3f3;
}

/* dndpanel */
.lf-dndpanel {
  position: absolute;
  margin: 5px;
  padding: 15px 5px;
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
}
.lf-dnd-item {
  margin-top: 5px;
  text-align: center;
  font-size: 14px;
}
.lf-dnd-shape {
  width: 36px;
  height: 36px;
  background-repeat: no-repeat;
  background-position: center center;
  cursor: grab;
  margin: 0 auto;
  opacity: 0.99;
}
/* 选区 */
.lf-selection-select {
  border: 2px dashed darkorange;
  position: absolute;
}
/* 缩略图 */
.lf-mini-map {
  border: 1px solid #93a3b4;
  background: rgba(255, 255, 255, 1);
  position: absolute;
  overflow: hidden;
  padding-top: 20px;
}
.lf-mini-map-graph {
  position: relative;
  overflow: hidden;
}
.lf-minimap-viewport {
  position: absolute;
  top: 0;
  left: 0;
  border: 2px solid rgb(24, 125, 255);
  cursor: grab;
}
.lf-mini-map-header {
  height: 18px;
  line-height: 18px;
  border-top: #bfbfbf solid 1px;
  border-bottom: #666666 solid 1px;
  text-shadow: 0 1px 0 rgb(255 255 255 / 33%);
  background-color: #a6a6a6;
  background-image: linear-gradient(to bottom,#b2b2b2,#9b9b9b);
  padding: 0px 10px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  border: 1px solid #999;
}
.lf-mini-map-close {
  position: absolute;
  right: 2px;
  top: 0px;
  width: 18px;
  height: 18px;
  cursor: pointer;
  background-size: contain;
  background-image: url('data:image/png;base64,');
}

/* 节点调整 */

.lf-resize-control-0{
  cursor: nw-resize;
}
.lf-resize-control-1{
  cursor: ne-resize;
}
.lf-resize-control-2{
  cursor: se-resize;
}
.lf-resize-control-3{
  cursor: sw-resize;
}
/* context-menu */
.lf-inner-context {
  display: none;
  position: absolute;
  /* display: flex; */
  flex-direction: column;
  flex-wrap: wrap;
  height: 90px;
}
.lf-context-item {
  padding: 2px 3px;
  width: 20px;
  height: 20px;
  margin: 0 2px 2px 0;
  box-sizing: content-box;
}
.lf-context-item:hover {
  background: rgba(201, 217, 216, 0.5);
}
.lf-context-img {
  width: 20px;
  height: 20px;
  cursor: pointer;
}
/* mind map */
.lf-mindmap_addIcon {
  margin-top: 10px;
}